$primary-color: #fe2c55;
$dark-color: #262626;
$gray-color: #eeeeee;
$light-color: #c32a67;
$text-color: #999999;

@-moz-keyframes "tiktok-feed-spin" {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes "tiktok-feed-spin" {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes "tiktok-feed-spin" {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-moz-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: "qlttf";
  src: url(data:font/opentype;charset=utf-8;base64,);
  src: url(data:font/opentype;charset=utf-8;base64,)
      format("embedded-opentype"),
    url(data:font/opentype;charset=utf-8;base64,)
      format("truetype"),
    url(data:font/opentype;charset=utf-8;base64,)
      format("woff"),
    url(data:font/opentype;charset=utf-8;base64,)
      format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="qlttf-icon-"],
[class*=" qlttf-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "qlttf" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.qlttf-icon-gallery:before {
  content: "\e90d";
}
.qlttf-icon-clock-o:before {
  content: "\e90c";
}
.qlttf-icon-upload:before {
  content: "\e900";
}
.qlttf-icon-share-o:before {
  content: "\e902";
}
.qlttf-icon-comment:before {
  content: "\e903";
}
.qlttf-icon-heart:before {
  content: "\e904";
}
.qlttf-icon-prev:before {
  content: "\e906";
}
.qlttf-icon-prev-o:before {
  content: "\e907";
}
.qlttf-icon-next:before {
  content: "\e908";
}
.qlttf-icon-next-o:before {
  content: "\e909";
}
.qlttf-icon-heart-o:before {
  content: "\e90a";
}
.qlttf-icon-comment-o:before {
  content: "\e90b";
}

.qlttf-icon-download:before {
  content: "\e900";
  display: inline-block;
  transform: rotate(180deg);
}

.tiktok-feed-feed {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1;
}

.tiktok-feed-feed {
  // Effects
  // ---------------------------------------------------------------------------

  .tiktok-feed-item,
  .tiktok-feed-video,
  .tiktok-feed-actions {
    opacity: 0;
  }

  &.loaded {
    .tiktok-feed-list {
      .tiktok-feed-item {
        transition: opacity 0.4s;
        opacity: 1;

        &.ig-image-loaded {
          .tiktok-feed-video {
            transition: all 0.4s;
            opacity: 1;
          }
        }
      }
    }

    .tiktok-feed-actions {
      transition: all 0.4s ease-in;
      opacity: 1;
    }
  }

  // Common
  // ---------------------------------------------------------------------------

  .tiktok-feed-spinner {
    position: relative;
    height: 60px;
    width: auto;
    box-sizing: content-box;
    background-size: 60px;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-image: url(data:image/svg+xml;base64,);
  }

  // Images
  // ---------------------------------------------------------------------------

  .tiktok-feed-list {
    &:after {
      content: "";
      display: block;
      clear: both;
    }

    .tiktok-feed-item {
      overflow: hidden;
      display: block;
      line-height: 0;

      &.tiktok-feed-cols-1 {
        width: 100%;
      }

      &.tiktok-feed-cols-2 {
        width: 50%;

        &.highlight {
          width: 100%;
        }
      }

      &.tiktok-feed-cols-3 {
        width: 33.333%;

        &.highlight {
          width: 66.666%;
        }
      }

      &.tiktok-feed-cols-4 {
        width: 25%;

        &.highlight {
          width: 50%;
        }
      }

      &.tiktok-feed-cols-5 {
        width: 20%;

        &.highlight {
          width: 40%;
        }
      }

      &.tiktok-feed-cols-6 {
        width: 16.666%;

        &.highlight {
          width: 33.333%;
        }
      }

      &.tiktok-feed-cols-7 {
        width: 14.285%;

        &.highlight {
          width: 42.855%;
        }
      }

      &.tiktok-feed-cols-8 {
        width: 12.5%;

        &.highlight {
          width: 37.5;
        }
      }

      &.tiktok-feed-cols-9 {
        width: 11.111%;

        &.highlight {
          width: 33.333%;
        }
      }

      &.tiktok-feed-cols-10 {
        width: 10%;

        &.highlight {
          width: 30%;
        }
      }

      &.tiktok-feed-cols-11 {
        width: 9.09%;

        &.highlight {
          width: 27.27%;
        }
      }

      &.tiktok-feed-cols-12 {
        width: 8.33%;

        &.highlight {
          width: 33.32%;
        }
      }

      &.tiktok-feed-cols-13 {
        width: 7.692%;

        &.highlight {
          width: 30.768%;
        }
      }

      &.tiktok-feed-cols-14 {
        width: 7.142%;

        &.highlight {
          width: 28.568%;
        }
      }

      &.tiktok-feed-cols-15 {
        width: 6.666%;

        &.highlight {
          width: 26.664%;
        }
      }

      &.tiktok-feed-cols-16 {
        width: 6.25%;

        &.highlight {
          width: 31.25%;
        }
      }

      &.tiktok-feed-cols-17 {
        width: 5.882%;

        &.highlight {
          width: 29.41%;
        }
      }

      &.tiktok-feed-cols-18 {
        width: 5.555%;
      }

      &.tiktok-feed-cols-19 {
        width: 5.263%;

        &.highlight {
          width: 33.33%;
        }
      }

      &.tiktok-feed-cols-20 {
        width: 5%;

        &.highlight {
          width: 30%;
        }
      }

      .tiktok-feed-item-wrap {
        width: 100%;
        //background-color: $gray-color;
        overflow: hidden;
      }

      .tiktok-feed-icon,
      .tiktok-feed-video {
        transition: all 0.3s;
      }

      .tiktok-feed-icon {
        position: absolute;
        z-index: 1;
        font-size: 16px;
        color: white;
        opacity: 0.66;
        top: 0;
        margin: 10px;
        right: 0;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.15);
        display: flex;
        text-decoration: none;

        &.tiktok-feed-video-play {
          top: 50%;
          left: 50%;
          margin: -12px auto auto -8px;
          font-size: 24px;
          width: 24px;
          height: 24px;
        }

        &.tiktok-feed-video-play-count {
          top: auto;
          bottom: 0;
          opacity: 0;
          right: auto;
          left: 0;
          pointer-events: none;
        }

        > span {
          margin: 0 5px;
          line-height: 16px;
          font-size: 13px;
          font-weight: 600;
          position: relative;
          top: -1px;
        }
      }

      .tiktok-feed-video-card {
        background-color: white;
        font-size: 12px;
        line-height: 1.2;
        ///padding: 0 .5em;
        font-weight: 600;
        width: 100%;
        //margin: 0 -1em -1em -1em;

        .ig-card-info,
        .ig-card-caption {
          display: flex;
          clear: both;
          margin: 0;
          padding: 1em 0;
        }

        .tiktok-feed-card-icons {
          margin: 0 0 0 auto;
        }
        .ig-card-link,
        .ig-card-comment_count,
        .ig-card-digg_count {
          i {
            font-weight: bold;
          }
        }

        .ig-card-info {
          border-bottom: 1px solid rgba(0, 0, 0, 0.05);
          font-size: 0.9em;

          .ig-card-date {
            display: inline-block;
            text-transform: uppercase;
            opacity: 0.6;
            /*float       : right;*/
            /*margin-left : auto;*/
          }

          .ig-card-comment_count,
          .ig-card-digg_count {
            display: inline-block;
            margin: 0;
          }
          .ig-card-date {
            display: inline-block;
            margin: 0 5px 0px 0;
          }
        }
      }

      .tiktok-feed-video-wrap {
        display: block;
        position: relative;
        text-align: center;
        background-color: $gray-color;
        cursor: pointer;

        .tiktok-feed-video {
          width: 100%;
          height: auto;
          overflow: hidden;
          vertical-align: middle;
        }

        .tiktok-feed-video-mask {
          position: absolute;
          content: "";
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          z-index: 8;
          transition: all 0.5s ease-out;
          opacity: 0;
          background: rgba(0, 122, 255, 0.55);
          width: 100%;
          height: 100%;
        }

        .tiktok-feed-video-mask-content {
          position: absolute;
          top: 50%;
          left: 0px;
          right: 0;
          margin: -10px 0 0 0;
          z-index: 9;
          width: 100%;
          color: white;
          transition: all 0.5s ease;
          opacity: 0;
          line-height: 20px;
          font-size: 18px;
          text-align: center;

          > span {
            padding: 0 4px;
            font-size: 15px;
            font-weight: 600;
            vertical-align: middle;
            line-height: 20px;

            > i {
              font-size: 12px;
              margin-right: 3px;
              font-weight: 800;
              color: $primary-color;
            }
          }
        }
      }
      &:hover {
        .tiktok-feed-icon {
          &.tiktok-feed-video-play-count {
            opacity: 0.8;
            pointer-events: all;
            z-index: 99;
            &:hover {
              opacity: 1;
            }
          }
          &.tiktok-feed-video-play,
          &.qlttf-icon-gallery {
            opacity: 0;
            pointer-events: none;
          }
        }

        .tiktok-feed-video-wrap {
          .tiktok-feed-video-mask-content {
            opacity: 1;
          }

          .tiktok-feed-video-mask {
            opacity: 1;
            transition: all 0.5s ease-out;
          }
        }
      }
    }
  }

  // Profile
  // ---------------------------------------------------------------------------

  .tiktok-feed-profile {
    width: auto;
    margin: 15px 0;

    &,
    .avatar,
    .info {
      height: 46px;
      display: flex;
      align-items: center;
    }

    img {
      border: none;
      width: 46px;
      height: 46px;
      margin-right: 10px;
      border-radius: 60px;
    }

    .avatar {
      margin: 0 10px 0 0;
    }

    .separator,
    .follow,
    .user {
      margin: 0;
      padding: 0;
      text-decoration: none;
      font-weight: 600;
      font-size: 16px;
      color: $dark-color;
      display: inline-block;
      vertical-align: middle;
      line-height: 18px;
    }

    .separator {
      color: $dark-color;
      margin-left: 4px;
      margin-right: 4px;
    }

    .follow {
      color: $primary-color;
      &:hover {
        color: $dark-color;
      }
    }

    .description {
      line-height: 18px;
      font-size: 12px;
      font-weight: 600;
    }
  }

  // Actions
  // ---------------------------------------------------------------------------

  .tiktok-feed-actions {
    text-align: center;
    margin: 15px 0px;

    .tiktok-feed-button {
      display: inline-block;
      margin: 0 3px;
      font-size: 14px;
      padding: 12px 24px;
      transition: all 0.3s;
      text-decoration: none;
      border-radius: 2px;
      background-color: $primary-color;
      color: white;
      vertical-align: middle;
      line-height: 16px;
      font-weight: 600;

      > i {
        margin: 0 8px 0 0;
        font-size: 12px;
      }

      > svg {
        margin: 0 8px 0 0;
        max-height: 14px;
        display: inline-block;
        width: auto;
        vertical-align: bottom;
      }

      &:focus,
      &:hover {
        box-shadow: inset 0 0 20px 20px rgba(255, 255, 255, 0.2);
      }

      &.follow {
        background-color: $primary-color;
      }

      &.load {
        background-color: $dark-color;
        text-transform: capitalize;
        cursor: pointer;
      }
    }
  }

  // Square
  // ---------------------------------------------------------------------------

  &.tiktok-feed-square {
    .tiktok-feed-list {
      .tiktok-feed-item {
        display: flex;
        justify-content: center;
        align-items: stretch;
        box-sizing: border-box;

        .tiktok-feed-video-wrap {
          display: flex;
          justify-content: center;
          align-items: center;
          //
          overflow: hidden;
          width: 100%;
          padding-top: 100%;

          .tiktok-feed-link {
            position: static;
          }

          .tiktok-feed-video {
            overflow: hidden;
            object-fit: cover;
            width: 100%;
            height: 100%;
            //
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
          }
        }
      }
    }
  }

  // Carousel
  // ---------------------------------------------------------------------------

  &[data-feed_layout="carousel"] {
    overflow: hidden;

    .tiktok-feed-list {
      display: flex;
      //align-items: center;
    }

    .swiper-button-next,
    .swiper-button-prev {
      transition: all 0.3s;
      background: none;
      top: 0;
      bottom: 0;
      height: 100%;
      margin: 0;
      background: transparent;
      width: 32px;
      display: flex;
      align-items: center;

      > i {
        text-align: center;
        color: $primary-color;
        width: 32px;
        font-size: 26px;
      }

      &:hover {
        background-color: rgba(0, 0, 0, 0.2);
      }
    }

    .swiper-button-prev {
      left: 0;
    }

    .swiper-button-next {
      right: 0;
    }

    .swiper-pagination-bullet-active {
      background-color: $primary-color;
    }
  }

  // Gallery
  // ---------------------------------------------------------------------------

  &[data-feed_layout="gallery"] {
    .tiktok-feed-list {
      display: flex;
      flex-wrap: wrap;
      //align-items: center;
      justify-content: start;
      overflow: hidden;
    }
  }

  // Responsive
  // -------------------------------------------------------------------------

  &[data-feed_layout="masonry"],
  &[data-feed_layout="gallery"] {
    .tiktok-feed-list {
      .tiktok-feed-item {
        @media screen and (max-width: 1023px) and (min-width: 768px) {
          min-width: 33.333%;
        }

        @media screen and (max-width: 767px) {
          min-width: 33.333%;
        }
        @media screen and (max-width: 480px) {
          min-width: 50%;
        }
      }
    }
  }

  // Masonry
  // -------------------------------------------------------------------------

  &[data-feed_layout="masonry"] {
    .tiktok-feed-list {
      .tiktok-feed-item {
        position: absolute;
      }
    }
  }
}

// Alert
// -----------------------------------------------------------------------------

.tiktok-feed-alert {
  background-color: $primary-color;
  padding: 15px;
  color: white;
  border-radius: 3px;
  margin: 0 0 15px 0;
  width: 100%;
  display: block;

  &::after,
  &::before {
    content: " ";
    display: table;
  }

  &::after {
    clear: both;
  }
  .button {
    float: right;
  }

  ul {
    padding: 0;
    margin: 0;
  }

  li {
    list-style: none outside !important;
    padding-left: 0 !important;
    margin-left: 0 !important;
  }
}

// Popup
// -----------------------------------------------------------------------------

.qlttf-mfp-wrap {
  @extend.tiktok-feed-feed;

  .mfp-arrow {
    font-family: "qlttf" !important;
    font-style: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 30px;
    font-weight: bold;

    &:after {
      display: none;
    }

    &:before {
      border: none;
    }

    &.mfp-arrow-left {
      &:before {
        content: "\e906";
      }
    }

    &.mfp-arrow-right {
      &:before {
        content: "\e908";
      }
    }
  }

  .mfp-container {
    cursor: pointer !important;

    .mfp-close {
      position: fixed;
      top: 30px;
      right: 10px;
      color: white;
      font-weight: normal;
      font-size: 30px;
      margin: 0;
      height: auto;
      line-height: 1;
      cursor: pointer !important;
      font-size: 24px;
      content: "Ãƒâ€”";
    }

    .mfp-content {
      width: auto;
      max-width: 90%;
      margin: 0 auto;

      /* .mfp-figure > .loader, .mfp-figure > .loader > img ,.mfp-figure > .loader  > video 
      .mfp-figure

      */

      .mfp-figure {
        &,
        > .loader,
        > .loader > img,
        > .loader > video {
          max-height: 70vh !important;
          max-width: 80vw !important;
          width: auto !important;
          background-color: black;
        }
        .loader > video {
          position: relative;
        }
        > .loader {
          position: relative;
          &:before {
            content: "";
            position: absolute;
            display: block;
            border: 5px solid #f3f3f3;
            border-top: 5px solid #fe2c55;
            border-radius: 50%;
            width: 40px;
            height: 40px;
            animation: spin 2s linear infinite;
            left: 50%;
            top: 50%;
            margin: -20px 0 0 -20px;
          }
        }
      }
    }

    .mfp-img {
      padding: 0;
    }

    .mfp-figure {
      position: relative;
      padding: 0;
      overflow: hidden !important;
      border-radius: 3px;

      &:after {
        display: none;
      }

      // Top & Bottom
      //&.top,
      //&.bottom {
      .mfp-bottom-bar {
        left: 0;
        right: 0;
        overflow-y: auto;
        overflow-x: hidden;
        width: 100%;
        min-height: 50px;
        max-height: 100px;
      }
      //}

      //&.bottom {
      padding-bottom: 50px;
      .mfp-bottom-bar {
        border-top: 1px solid $gray-color;
        bottom: 0;
      }
      //}

      &.top {
        padding-top: 50px;
        .mfp-bottom-bar {
          border-bottom: 1px solid $gray-color;
          top: 0;
        }
      }

      @media (min-width: 767px) {
        // Right & Left
        &.left,
        &.right {
          padding-bottom: unset;
          .mfp-bottom-bar {
            border: unset;
            top: unset;
            bottom: unset;
            left: unset;
            right: unset;
            min-height: unset;
            max-height: unset;
            width: 300px;
          }
        }

        &.left {
          padding-left: 300px;
          .mfp-bottom-bar {
            border-right: 1px solid $gray-color;
            top: 0;
            bottom: 0;
            left: 0;
          }
        }

        &.right {
          padding-right: 300px;
          .mfp-bottom-bar {
            border-left: 1px solid $gray-color;
            top: 0;
            bottom: 0;
            right: 0;
          }
        }
      }
    }

    .mfp-bottom-bar {
      top: auto;
      bottom: auto;
      right: auto;
      left: auto;
      background-color: white;
      margin: 0;
      padding: 0;
      line-height: 1.4;
      text-align: left;

      .mfp-title {
        font-size: 13px;
        font-weight: 400;
        color: $dark-color;
        padding: 0;
        /*
                    a {
                      font-weight: 600;
                      color: #fe2c55; 
                      text-decoration: none;
          
                      &:hover {
                        color:$dark-color;
                      }
          
                    }*/

        .mfp-info,
        .mfp-icons,
        .mfp-author,
        .mfp-text {
          display: block;
          clear: both;
          margin: 0;
          padding: 15px;
        }

        .mfp-text {
          padding-top: 0;
          padding-bottom: 0;
          margin-bottom: 15px;

          > a {
            text-decoration: none;
            color: $primary-color;
            font-weight: 600;

            &:hover {
              color: $text-color;
            }
          }
        }

        .mfp-icons {
          .mfp-link,
          .mfp-counter {
            position: inherit;
            display: inline-block;
            font-size: 12px;
            font-weight: 400;
            color: $text-color;
            margin: 0 15px 0 0;
            line-height: 1;
          }

          .mfp-link {
            &:hover {
              color: $primary-color;
            }

            > svg {
              display: inline-block;
              vertical-align: text-bottom;
            }
          }
        }

        .mfp-link,
        .mfp-comment_count,
        .mfp-digg_count {
          i {
            margin: 0 4px 0px 0;
            //font-size: 11px;
            font-weight: bold;
            //position: relative;
            //top: 1px;
          }
        }

        .mfp-info {
          border-top: 1px solid $gray-color;

          .mfp-date {
            text-transform: uppercase;
            font-size: 12px;
            color: $text-color;
            float: right;
            position: relative;
            bottom: -1px;
          }

          .mfp-date,
          .mfp-comment_count,
          .mfp-digg_count {
            display: inline-block;
            margin: 0 5px 0px 0;
          }
        }

        .mfp-author {
          display: flex;
          align-items: center;
          border-bottom: 1px solid $gray-color;
          width: auto;
          font-weight: bold;

          > img {
            border: none;
            width: 48px;
            height: 48px;
            margin-right: 15px;
            border-radius: 40px;
          }

          > div {
            > a {
              margin: 0;
              padding: 0;
              text-decoration: none;
              font-size: 13px;
              color: $text-color;
            }

            > span {
              display: block;
              text-transform: uppercase;
              color: $dark-color;
              font-weight: 700;
            }
          }
        }
      }
    }
  }
}

// IE8
// -----------------------------------------------------------------------------
.tiktok-gallery-ie-8 {
  .tiktok-feed-spinner {
    display: none;
  }
  tiktok-feed-list {
    .tiktok-feed-item {
      .tiktok-feed-video-wrap {
        &:hover {
          &:after {
            background: none;
          }
        }
      }
      max-width: 33.333%;
      float: left;
    }
  }
  .swiper-button-prev {
    display: none;
  }
  .swiper-button-next {
    display: none;
  }
}

// IE9
// -----------------------------------------------------------------------------

.tiktok-gallery-ie-9 {
  .tiktok-feed-spinner {
    display: none;
  }
  tiktok-feed-list {
    .tiktok-feed-item {
      max-width: 33.333%;
      float: left;
    }
  }
  .swiper-button-prev {
    display: none;
  }
  .swiper-button-next {
    display: none;
  }
}

.igblock-wrap-IElte8 {
  tiktok-feed-list {
    .tiktok-feed-item {
      width: 25%;
      float: left;
    }
    &:after {
      clear: both;
      display: block;
      content: "";
    }
  }
}

.tiktok-loading {
  display: block;
  margin: 6px;
  background-size: 48px;
  width: 48px;
  height: 48px;
  -webkit-animation: tiktok-loading steps(60, start) 1s infinite;
  animation: tiktok-loading steps(60, start) 1s infinite;
}
